import { Component, ViewEncapsulation, Input, AfterViewInit } from '@angular/core';
import { JSONBuilder } from '../../../services/JSONBuilder.service';
declare var jQuery: any;
declare var filepicker: any;
declare var observe: any;
@Component({
  selector: 'common-editor',
  template: `   
            <div class="ques-textarea-parent">
                <span class="form-label">Question Title:</span> 
                <div class="help-tip editor-helptip" *ngIf="jsonBuilderHelper.getJSONBuilt().template == 'one-page-card' && control.imageVisible">
                    <i class="material-icons">info_outline</i>
                    <div class="help-checktip">Recommended Dimensions are 800px by 300px.</div>
                </div>
                <div *ngIf="jsonBuilderHelper.getJSONBuilt().template == 'one-page-card'">
                  <div class="switch">
                    <label>
                      <input type="checkbox" name="properties-checkbox" class="show-check" [checked]="control.imageVisible" (change)="control.imageVisible = !control.imageVisible" data-size="mini" data-on-color="blue">
                      <span class="lever-image"><i class="material-icons"></i></span>
                    </label>
                  </div>
                </div>
                <textarea class="form-text ques-title" [(ngModel)]="control.props.title" (keyup)="textAreaAdjust($event)" (keypress)="textEnd($event)" row="1"></textarea>
                <div *ngIf="jsonBuilderHelper.getJSONBuilt().template == 'one-page-card'">
                <div class="landing-inputfile div-check" *ngIf="control.imageVisible">
                  <div class="col-xs-12 no-padding">
                    <div class="input-group">
                      <input
                        type="text"
                        class="file-input"
                        [(ngModel)]="control.imageURL"
                        placeholder="No file"
                        readonly
                      >
                      <label class="input-group-btn">
                        <span class="btn btn-default btn-flat btn-basic2 btn-upload" data-fp-apikey="ANQWcFDQRUiGfBqjfgINQz" (click)="uploadImage(control);">
                        Image <input type=""  style="display: none;">
                        </span>
                      </label>
                    </div>
                  </div>
                </div>
              </div>
                
            </div>        
    `,
  encapsulation: ViewEncapsulation.None,
})

export class CommonEditor implements AfterViewInit {
  @Input() control: any;
  filePickerKey: any = 'A3ygIw4hISSCdApqW4SAwz';

  ngAfterViewInit() {
    setTimeout(function () { jQuery('.ques-title').css('height', jQuery('.ques-title').prop('scrollHeight')); }, 1);
  }
  constructor(private jsonBuilderHelper: JSONBuilder) {
    this.control = jsonBuilderHelper.getSelectedControl();
  }
  textAreaAdjust(event: any) {
    jQuery('.ques-title').css('height', jQuery('.ques-title').prop('scrollHeight'));
  }
  textEnd(event: any) {
    if (event.keyCode === 10 || event.keyCode === 13)
      event.preventDefault();
  }

  uploadImage(control: any) {
    filepicker.setKey(this.filePickerKey);
    filepicker.pick(
      {
        mimetypes: ['image/*'],
        imageQuality: 50
      },
      (InkBlob: any) => {
        control.imageURL = InkBlob.url;
        jQuery('#filepicker_dialog_container').find('a').click();
      },
      (FPError: any) => {
        console.log(FPError.toString());
      }
    );
  }
}




